.main-content {
    position: relative;
    width: 100%;
    height: 100vh; /* Full viewport height */
    overflow: hidden; /* Prevent overflow */
}

.background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh; /* Ensures the image covers the entire screen */
    object-fit: cover; /* Ensures the image covers the entire area */
    z-index: -1; /* Makes sure the image stays in the background */
}

.text-section {
    position: relative;
    z-index: 1; /* Ensures text stays on top of the background image */
    color: white; /* Change text color if necessary */
    padding: 20px;
    max-width: 800px; /* Adjust as necessary */
    margin: 0 auto;
    text-align: center;
}

.headline {
    font-size: 3rem; /* Adjust size as needed */
    margin-bottom: 20px;
}

.description {
    font-size: 1.25rem; /* Adjust size as needed */
    margin-bottom: 30px;
}

.cta-button {
    padding: 15px 30px;
    background-color: #007BFF;
    color: white;
    text-decoration: none;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
}

.cta-button:hover {
    background-color: #0056b3;
}

.image-section {
    position: relative;
    z-index: 1;
    text-align: center;
    padding: 20px;
}

.image {
    max-width: 100%;
    height: auto;
}

.landingnavbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: rgba(255, 255, 255, 0.6); /* Semi-transparent white background */
    color: #1a1a1a;
    font-family: 'Arial', sans-serif;
    position: sticky;
    top: 0;
    z-index: 1000;
    backdrop-filter: blur(10px); /* Optional: adds a blur effect to the background */
  }
  
  .landingnavbar-brand {
    font-size: 1.5rem;
    font-weight: bold;
    text-decoration: none;
    color: #fff;
  }
  
  .menu-btn {
    display: none; 
    font-size: 1.5rem;
    cursor: pointer;
  }
  
